    border-color: #1a1b211d;
    border-width: 3px;
}

.toggle-container{
    text-align: right;
    margin-right: 20px;
}

.toggle{
    --width: 50px;
    --height: calc(var(--width) / 2);
    --border-radius: calc(var(--height) / 2);
    display: inline-block;
    cursor: pointer;
}
  
.toggle__input {
    display: none;
}
  
.toggle__fill {
    position: relative;
    width: var(--width);
    height: var(--height);
    border-radius: var(--border-radius);
    background: #dddddd;
    transition: background 0.2s;
}
  
.toggle__input:checked ~ .toggle__fill {
    background: #009578;
}
  
.toggle__fill::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: var(--height);
    width: var(--height);
    background: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    border-radius: var(--border-radius);
    transition: transform 0.2s;
}
  
.toggle__input:checked ~ .toggle__fill::after {
    transform: translateX(var(--height));
}

.toggle-text{
    margin-right: 10px;
    line-height: 1px;
}